@import "../../../styles/setup.scss";

.button-container {
  display: flex;
  justify-content: center;

  .btn-submit {
    width: 100%;
    height: 35px;
    // @media only screen and (max-width: 735px) {
    //   width: 100% !important;
    // }
    background-color: transparent;
    font-weight: 500;
    font-size: 16px;
    font-family: $equal-font;
    font-style: normal;
    line-height: 24px;
    border: 1px solid $stratos-border-color;
    border-radius: 12px;
    &:hover {
      color: $stratos-green-dark;
      border: 2px solid $stratos-green-ligth;
      cursor: pointer;
    }

    &.active {
      background: #2e6b52;
      border: none;
      color: #fff;

      &:hover {
        background: white;
        color: $stratos-green-darker;
        border: 2px solid $stratos-green-ligth;
      }
    }

    &.inactive {
      color: #fff;
      cursor: not-allowed;
      background-color: #c0c0c0;
      border: 1px solid #c0c0c0;
    }
  }
}
